@import '../../App.scss';
.hero-conatiner{

    .hero{
        // height: calc(100vh - 100px);
        height: 100dvh;
        background: linear-gradient(180deg, #0c0c1d, #111132);
        overflow: hidden;
        position: relative;
        @include mobile {
            display: flex;
        flex-direction: column;
        }
    
        .wrapper{
            // max-width: 1366px;
            width: 50%;
            // border: 1px solid yellow;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            // margin: auto ;
            @include mobile {
                // max-width: 366px;
            // height: auto;
            padding-top: 50px;
            height: 60%;
            width: 100%;
            }
        
            .text-container{
                width: 50%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                gap:40px;
                @include mobile {
                    width: 100%;
                    // height: 50%;
                    gap:20px;
                }

                h2{
                    font-size: 30px;
                    letter-spacing: 10px;
                    color: rebeccapurple ;
                    text-transform: uppercase;
                    @include mobile {
                    margin: 0;
                        text-align: center;

                    }
                }
                h1{
                    font-size: 88px;
                    color: white;
                    @include mobile {
                        font-size: 40px;
                        text-align: center;
                        margin: 0;
                    }
                }
                .buttons{
                    z-index: 10;
                    display: flex;
                    gap: 10px;
                    @include mobile {
                    
                        flex-direction: column;
                        width: 80%;
                        margin: 0 auto;
                    }
                    .button-header{
                        padding: 20px;
                        border: 1px solid white;
                        border-radius: 10px;
                        background-color: transparent;
                        color: #fff;
                        cursor: pointer;
                        font-weight: 300;
                        text-decoration: none;
                        @include mobile {
                        text-align: center;
                        }

                    }
                }
                img{
                    width: 50px;
                }
            }
        }
        .sliding-texte-container{
            font-size: 50vh;
            position: absolute;
            bottom: -120px;
            white-space: nowrap;
            color: #ffffff09;
            width: 50%;
            font-weight: bold;
            
        }
        .image-container{
            height: 100%;
            width: 50%;
            position: absolute;
            top: 0;
            right: 0;
            // border: 1px solid red;
            @include mobile {
                // position: inherit;
                right: inherit;
                top: inherit;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 45%;
                // margin: 0 auto;
               display: flex;
            //    border: 1px solid salmon;
               justify-content: end;
                
            }
        
            img{
               height: 100%;
              
               @include mobile {

                width: 100%;
                margin-left: 20px;
                object-fit: cover;
   
             
            }
            }


        }
    }
}